import { useState } from 'react';
import { Button, MenuItem, Select, FormControl, InputLabel } from '@material-ui/core';
import { exportByDay } from './api-exports'; // Assuming you have an API file for making requests
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import Fraction from 'fraction.js';

const ExportByDay = () => {
    const [day, setDay] = useState('');
    const [month, setMonth] = useState('');

    const decToFrac = (decimal) => {
        const maxDenominator = 10000;
        const fraction = new Fraction(decimal).toFraction(maxDenominator);
        return fraction.toString();
    }

    const handleData = (data) => {
        if (!data || data.length === 0) {
            alert('Khng c d liu  xut');
            return;
        }
        
        const headerMapping = {
            orderId: "Phiu yu cu",
            customerName: "n v",
            size: "Kch thc",
            pricePerUnit: "Tng tin"
        }

        const formattedData = data.map((item) => {
            item.size = decToFrac(item.size ? item.size.$numberDecimal : 0);
            return {
                'Phiu yu cu': item.orderId,
                'n v': item.customerName,
                'Kch thc': item.size,
                'Tng tin': item.pricePerUnit
            }
        });

        const worksheet = XLSX.utils.json_to_sheet(formattedData, { header: Object.values(headerMapping) });
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        const dataFile = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8' });
        saveAs(dataFile, `thongkethang${month}-ngay${day}.xlsx`);
    }

    const handleExport = async () => {
        try{
            const data = await exportByDay(month, day);
            handleData(data);
        }
        catch(error){
            console.error('There was a problem with the fetch operation:', error);
            // Handle error as needed
        }
    }

    return (
        <div style={{margin: '10px'}}>
            <FormControl variant="outlined" style={{ marginRight: '10px', width: '200px' }}>
                <InputLabel id="month-label">Chn thng</InputLabel>
                <Select
                    labelId="month-label"
                    id="month-select"
                    label="Month"
                    value={month}
                    onChange={(e) => setMonth(e.target.value)}
                >
                    <MenuItem value={1}>Thng 1</MenuItem>
                    <MenuItem value={2}>Thng 2</MenuItem>
                    <MenuItem value={3}>Thng 3</MenuItem>
                    <MenuItem value={4}>Thng 4</MenuItem>
                    <MenuItem value={5}>Thng 5</MenuItem>
                    <MenuItem value={6}>Thng 6</MenuItem>
                    <MenuItem value={7}>Thng 7</MenuItem>
                    <MenuItem value={8}>Thng 8</MenuItem>
                    <MenuItem value={9}>Thng 9</MenuItem>
                    <MenuItem value={10}>Thng 10</MenuItem>
                    <MenuItem value={11}>Thng 11</MenuItem>
                    <MenuItem value={12}>Thng 12</MenuItem>
                </Select>
            </FormControl>
            <FormControl variant="outlined" style={{ marginRight: '10px', width: '200px' }}>
                <InputLabel id="day-label">Chn ngy</InputLabel>
                <Select
                    labelId="day-label"
                    id="day-select"
                    label="Day"
                    value={day}
                    onChange={(e) => setDay(e.target.value)}
                >
                    <MenuItem value={1}>1</MenuItem>
                    <MenuItem value={2}>2</MenuItem>
                    <MenuItem value={3}>3</MenuItem>
                    <MenuItem value={4}>4</MenuItem>
                    <MenuItem value={5}>5</MenuItem>
                    <MenuItem value={6}>6</MenuItem>
                    <MenuItem value={7}>7</MenuItem>
                    <MenuItem value={8}>8</MenuItem>
                    <MenuItem value={9}>9</MenuItem>
                    <MenuItem value={10}>10</MenuItem>
                    <MenuItem value={11}>11</MenuItem>
                    <MenuItem value={12}>12</MenuItem>
                    <MenuItem value={13}>13</MenuItem>
                    <MenuItem value={14}>14</MenuItem>
                    <MenuItem value={15}>15</MenuItem>
                    <MenuItem value={16}>16</MenuItem>
                    <MenuItem value={17}>17</MenuItem>
                    <MenuItem value={18}>18</MenuItem>
                    <MenuItem value={19}>19</MenuItem>
                    <MenuItem value={20}>20</MenuItem>
                    <MenuItem value={21}>21</MenuItem>
                    <MenuItem value={22}>22</MenuItem>
                    <MenuItem value={23}>23</MenuItem>
                    <MenuItem value={24}>24</MenuItem>
                    <MenuItem value={25}>25</MenuItem>
                    <MenuItem value={26}>26</MenuItem>
                    <MenuItem value={27}>27</MenuItem>
                    <MenuItem value={28}>28</MenuItem>
                    <MenuItem value={29}>29</MenuItem>
                    <MenuItem value={30}>30</MenuItem>
                    <MenuItem value={31}>31</MenuItem>
                </Select>
            </FormControl>
            <Button variant="contained"
                color="primary"
                onClick={handleExport}>Xut</Button>
        </div>
    )             
}

export default ExportByDay;